import React from 'react'
import {User} from '../../user'
import {ChForm} from '../../modals/modal'
import {Notice} from '../../notice'
import Img from '../../../images/demo.png'

const user = new User();

class OrdersTab extends React.Component{

    state = {
        openForm: false,
        group: null,
        singleOrder: false,
        upd: new Date().toLocaleString(),
    }

    componentDidMount(){
        this.upd();
    }

    upd = () => {
        this.setState({notice: 'loading'})
        // Create html query
        let formData = new FormData();
        formData.append('user', user.id);
        formData.append('action', 'getorders');
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    if( res.result === 'error' ){
                        this.setState({
                            notice: 'error'
                        });
                    }
                    else{
                        this.setState(
                            {
                                orders: res,
                                upd: new Date().toLocaleString(),
                                notice: null
                            }
                        )
                    }
                },
                (error) => {
                    this.setState({
                        notice: 'error'
                    });
                }
            )
    }

    ifSuccess = () => {
        this.setState(
            {
                modalAction: 'add',
                notice: null,
                openForm: false,
            },
            this.upd()
        )
    }

    displayModal = (e) => {
        this.setState(
            {
                openForm: true,
                modalAction: 'addterm'
            }
        );
    }

    closeModal = () => {
        this.setState({ openForm: false })
    }

    openOrderInfo = (e) => {
        const id = e.target.closest('tr').dataset.orderid;
        let formData = new FormData();
        formData.append('user', user.id);
        formData.append('action', 'getorderinfo');
        formData.append('orderid', id);
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    if( res.result === 'ok' ){
                        this.setState({
                            singleOrder: res.message
                        });
                    }
                    else{
                        this.setState(
                            {
                                notice: 'error'
                            }
                        )
                    }
                },
                (error) => {
                    this.setState({
                        notice: 'error'
                    });
                }
            )
    }

    closeOrderInfo = () => {
        this.setState({singleOrder: false})
    }


    render(){
        const groups = this.state.orders;
        const item = this.state.group;
        const table = groups !== undefined ? groups.map((row) => 
                <TableRow
                    key={row.id}
                    row={row}
                    upd={this.upd}
                    openOrderInfo={this.openOrderInfo}
                />) : [];
        const fields = [
            {
                editable: true,
                required: true,
                label: '',
                type: 'text',
                fieldname: 'title',
                caption: ' 30 ',
                init: item && item.name
            },
            {
                editable: true,
                required: false,
                label: '',
                type: 'textarea',
                fieldname: 'comment',
                caption: ' 300 ',
                init: item && item.comment
            },
        ]
        const form = this.state.openForm
            && <ChForm
                type="modal"
                open={this.state.openForm}
                action={this.state.modalAction}
                close={this.closeModal}
                success={this.ifSuccess}
                fields={fields}
                item={item}
                />
        if( !this.state.singleOrder ){
            return(
                <div id="groups-tab" className="main-tab">
                    <table className="table table-hover">
                        <thead>
                            <tr className="table-dark">
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {table.length > 0 ? table : <tr><td colSpan="7"> </td></tr>}
                        </tbody>
                    </table>
                    {form}
                    <Notice status={this.state.notice} />
                </div>
            )
        }
        else{
            return(
                <>
                <div id="groups-tab" className="main-tab">
                    <OrderInfo order={this.state.singleOrder} close={this.closeOrderInfo} />
                </div>
                <Notice status={this.state.notice} />
                </>
            )
        }
    }
}

class TableRow extends React.Component{
    render(){
        const g = this.props.row;
        return(
            <tr data-orderid={g.id} onClick={this.props.openOrderInfo}>
                <td className="text-info pointer">#{g.id} {g.name}</td>
                <td>{g.session}</td>
                <td>{g.group}</td>
                <td>{g.cart.total} <i className="fas fa-ruble-sign"></i></td>
                <td>{g.status}</td>
                <td>{g.date}</td>
                <td style={{maxWidth: '100px', lineHeight: 1}}><small>{g.comment}</small></td>
            </tr>
        )
    }
}

class OrderInfo extends React.Component{

    render(){
        let items = [];
        const order = this.props.order;
        const meta = order.meta;
        const cart = order.cart;
        
        for(let item in cart.items){
            let path = cart.items[item].itemPath;
            let title = cart.items[item].itemTitle;
            for(let f in cart.items[item].formats){
                items.push( <OrderInfoItem
                                key={title+cart.items[item].formats[f].size}
                                path={path}
                                format={cart.items[item].formats[f]}
                                title={title}
                            />
                           );
            }
        }
        return(
            <div id="single-order" className="row">
                <button onClick={this.props.close} className="btn btn-outline-info btn-sm mb20"><i className="fas fa-arrow-left"></i>   </button>
                <div className="col-12 flex-center justify-between single-order__title">
                    <h4>#{order.id} {order.title}</h4>
                    <div className="mb10">
                        <span className="pr10"><strong>: </strong>{order.date}</span>
                        <span className="text-success"><strong>: </strong>{order.date}</span>
                    </div>
                </div>
                <div className="col-3">
                    <p><strong>:</strong> <a href={'tel:'+meta.phone}>{meta.phone}</a></p>
                    <p><strong>Email:</strong> <a href={'mail:'+meta.email}>{meta.email}</a></p>
                </div>
                <div className="col-3">
                    <p><strong>: </strong>{order.group}</p>
                    <p><strong>: </strong>{order.session}</p>
                </div>
                <div className="col-3">
                    <p><strong>: </strong>{meta.confirm == 'on' ? <strong className="text-success"></strong> : <strong className="text-alert"> </strong>}</p>
                    <p><strong>: </strong>{meta.payment}</p>
                </div>
                <div className="col-12 flex-center justify-between single-order__title mt20">
                    <h4>  </h4>
                    <div className="mb10">
                        <span className="m5">: <strong>{cart.count} .</strong></span>
                        <span className="m5"> : <strong>{cart.total} <i class="fas fa-ruble-sign"></i></strong></span>
                    </div>
                </div>
                <div className="single-order__items">
                    {items}
                </div>
            </div>
        )
    }
}

class OrderInfoItem extends React.Component{

    state = {
        fotoBin: false
    }

    componentDidMount(){
        if(!this.state.fotoBin){
            let formData = new FormData();
            formData.append('path', this.props.path);
            formData.append('user', user.id);
            formData.append('action', 'getthumb');
            fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
                .then(res => res.json())
                .then(
                    (res) => {
                        if( res['result'] === 'error' ){
                            //...
                        }
                        else if( res['result'] === 'ok' ){
                            this.setState({fotoBin: res['message']});
                        }
                        else{
                            //...
                        }
                    },
                    (error) => {
                        this.setState({
                            notice: 'error'
                        });
                    }
                )
        }
    }

    render(){
        const f = this.props.format;
        const classes = this.state.fotoBin ? 'yd-foto' : 'yd-foto loading';
        return(
            <div className="cart-item__wrapper">
                <div className="cart-item__title">{this.props.title}</div>
                <div className="cart-item__img">
                    <img
                        className={classes}
                        data-title={this.props.title}
                        src={this.state.fotoBin ? this.state.fotoBin : Img}
                        alt="choose.ru"
                    />
                </div>
                <div className="cart-item__info">
                    <span><small>:</small> {f.type}</span>
                    <span><small>:</small> {f.size}</span>
                    <span><small>:</small> {f.cost}.0</span>
                    <span><small>-:</small> {f.qty}</span>
                    <span><small>:</small> {f.cost*f.qty}.0 <i className="fons80 fas fa-ruble-sign"></i></span>
                </div>
            </div>
        )
    }
}

export { OrdersTab }