import React from 'react'
import {User} from '../../user'
import {ChForm} from '../../modals/modal'
import {Notice} from '../../notice'
import ReactTooltip from 'react-tooltip';
import { InlineTips } from '../../helper';

const user = new User();

class CollectionTab extends React.Component{

    state = {
        posts: [],
        groupsList: false,
        openForm: false,
        post: null,
        upd: new Date().toLocaleString(),
    }

    componentDidMount(){
        this.upd();
    }

    upd = () => {
        this.setState({notice: 'loading'})

        // Create html query
        let formData = new FormData();
        formData.append('user', user.id);
        formData.append('action', 'getposts');
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    if( res.result === 'error' ){
                        this.setState({
                            notice: 'error'
                        });
                    }
                    else{
                        this.setState(
                            { 
                                posts: res,
                                upd: new Date().toLocaleString(),
                                notice: null
                            }
                        )
                    }
                },
                (error) => {
                    this.setState({
                        notice: 'error'
                    });
                }
            )
    }

    ifSuccess = () => {
        this.setState(
            {
                modalAction: 'add',
                notice: null,
                openForm: false,
            },
            this.upd()
        )
    }

    displayModal = (e) => {
        this.setState(
            {
                openForm: true,
                modalAction: 'addpost'
            }
        );
    }

    closeModal = () => {
        this.setState({ openForm: false })
    }

    displayUpdModal = (e) => {
        const id = e.target.closest('.btn').dataset.id;
        let post = false;
        this.state.posts.forEach((el,i) => {
            if(el.id === +id){
                post = el;
            }
        })
        this.setState(
            {
                openForm: true,
                modalAction: 'updpost',
                post: post,
            }
        );
    }

    getUserGroupsSelectOptions = () => {
        //    
        if( this.state.groupsList )
            return this.state.groupsList;
        
        let formData = new FormData();
        let groupsList = [{ id: 1, name: ' ' }];
        formData.append('user', user.id);
        formData.append('action', 'getterms');
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    res.forEach((g) =>
                        groupsList.push({
                            id: g.id,
                            name: g.name
                        })  
                    )
                    this.setState({groupsList: groupsList})
                },
                (error) => {
                    // ...
                }
            )        
        return groupsList;
    }

    render(){

        if(user.formats.length === 0){
            return(
                <div className="alert alert-inline alert-warning">
                    <h4 className="alert-heading">!</h4>
                    <p className="mb-0">,    ,     <strong></strong>.</p>
                </div>
            )
        }

        const posts = this.state.posts;
        const item = this.state.post;
        const lustUpdTime = this.state.upd;
        const table = posts.map((row) => 
                <TableRow
                    key={row.id}
                    row={row}
                    upd={this.upd}
                    displayUpdModal={this.displayUpdModal}
                />)
        let fields = [
            {
                editable: true,
                required: true,
                label: '',
                col: 12,
                type: 'text',
                fieldname: 'title',
                caption: ' 30 ',
                init: item ? item.name : ''
            },
            {
                editable: true,
                required: true,
                label: '',
                col: 6,
                type: 'select',
                options: this.getUserGroupsSelectOptions(),
                fieldname: 'group',
                caption: '',
                init: item ? item.groupid : ''
            },
            {
                editable: false,
                required: false,
                label: '',
                col: 6,
                type: 'text',
                fieldname: 'password',
                caption: ' ',
                init: item ? item.password : ''
            },
            {
                type: 'formatsList',
                col: 12,
                fieldname: ''
            },
        ]
        const form = this.state.openForm
            && <ChForm
                type="modal"
                open={this.state.openForm}
                action={this.state.modalAction}
                close={this.closeModal}
                success={this.ifSuccess}
                fields={fields}
                item={item}
                />
        return(
            <div id="collections-tab" className="main-tab">
                <div className="tab_header display-flex justify-between mb1">
                    <small className="text-muted">upd: { lustUpdTime }</small>
                    <button
                        onClick={this.displayModal}
                        className="btn btn-outline-primary btn-sm"
                    > </button>
                </div>
                <table className="table table-hover">
                    <thead>
                        <tr className="table-dark">
                            <th></th>
                            <th></th>
                            <th></th>
                            <th> </th>
                            <th></th>
                            <th> / </th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {table}
                    </tbody>
                </table>
                
                {form}

                <InlineTips title=" :">
                    <h5 className="alert-heading text-warning">!</h5>
                    <ol>
                        <li> <strong className="text-info">  </strong>            <strong className="text-info"></strong>   <strong className="text-info"> </strong>.<br />      .</li>
                    </ol>
                    <h5> </h5>
                    <ol>
                        <li><strong></strong>         .  ,           .</li>
                        <li>  <strong> </strong>   ,    ,           ,       </li>
                        <li> <i className="fas fa-sync-alt text-success"></i> -  <strong>  </strong>     .           ,      .</li>
                    </ol>
                </InlineTips>
                
                <Notice status={this.state.notice} />
            </div>
        )
    }
}

class TableRow extends React.Component{
    render(){
        const g = this.props.row;
        //console.log(g)
        return(
            <tr>
                <td>{g.name}</td>
                <td>{g.group}</td>
                <td>{g.status}</td>
                <td>{g.date}</td>
                <td>{g.password}</td>
                <td>{g.groups.groupCount} / {g.groups.fotoCount}</td>
                <td>
                    <RowOptions 
                        id={g.id}
                        path={'/'+g.group+'/'+g.name}
                        upd={this.props.upd}
                        displayUpdModal={this.props.displayUpdModal}
                    />
                </td>
            </tr>
        )
    }
}

class RowOptions extends React.Component{

    state = {
        notice: null,
    }

    prepareImagesFromDrive = (e) => {
        e.preventDefault();
        this.notice = 'loading';
        
        let formData = new FormData();
        formData.append('user', user.id);
        formData.append('action', 'prepare_images');
        formData.append('postid', this.props.id);
        formData.append('path', this.props.path);
        
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    if(res.result === 'ok'){
                        this.props.upd();
                        this.notice = null;
                    }
                    else{
                        this.notice = 'error';
                    }
                },
                (error) => {
                    this.notice = 'error'
                }
            )    
    }

    removePost = (e) => {
        e.preventDefault();

        const prepare = window.confirm('   !');
        if( !prepare )
            return;
        this.notice = 'loading';
        let formData = new FormData();
        formData.append('user', user.id);
        formData.append('action', 'delete_post');
        formData.append('postid', this.props.id);
        formData.append('path', this.props.path);
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())
            .then(
                (res) => {
                    if(res.result === 'ok'){
                        this.props.upd();
                        this.notice = null
                    }
                    else{
                        this.notice = 'error';
                    }
                },
                (error) => {
                    this.notice = 'error'
                }
            )    
    }
    render(){
        return(
            <React.Fragment>
                <ReactTooltip type="info" />
                <button
                    onClick={this.props.displayUpdModal}
                    data-id={this.props.id}
                    type="button"
                    className="btn btn-outline-info mr2"
                    data-tip=" "
                >
                    <i className="fas fa-cog"></i>
                </button>
                <button
                    onClick={this.prepareImagesFromDrive}
                    data-id={this.props.id}
                    type="button"
                    className="btn btn-outline-success mr2"
                    data-tip=" "
                >
                    <i className="fas fa-sync-alt"></i>
                </button>
                <button
                    onClick={this.removePost}
                    type="button"
                    className="btn btn-outline-danger mr2"
                    data-tip=" "
                >
                    <i  className="far fa-times-circle"></i>
                </button>
                <Notice status={this.notice} />
            </React.Fragment>
        )
    }
}

export { CollectionTab }