import React from 'react'
import {User} from '../../user'
import {ChForm} from '../../modals/modal'
import {Notice} from '../../notice'

const user = new User();

class GroupsTab extends React.Component{

    state = {
        groups: [],
        openForm: false,
        group: null,
        upd: new Date().toLocaleString(),
    }

    componentDidMount(){
        this.upd();
    }

    upd = () => {
        this.setState({notice: 'loading'})

        // Create html query
        let formData = new FormData();
        formData.append('user', user.id);
        formData.append('action', 'getterms');
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    if( res.result === 'error' ){
                        this.setState({
                            notice: 'error'
                        });
                    }
                    else{
                        this.setState(
                            { 
                                groups: res,
                                upd: new Date().toLocaleString(),
                                notice: null
                            }
                        )
                    }
                },
                (error) => {
                    this.setState({
                        notice: 'error'
                    });
                }
            )
    }

    ifSuccess = () => {
        this.setState(
            {
                modalAction: 'add',
                notice: null,
                openForm: false,
            },
            this.upd()
        )
    }

    displayModal = (e) => {
        this.setState(
            {
                openForm: true,
                modalAction: 'addterm'
            }
        );
    }

    closeModal = () => {
        this.setState({ openForm: false })
    }

    displayUpdModal = (e) => {
        const id = e.target.closest('.btn').dataset.id;
        let group = false;
        this.state.groups.forEach((el,i) => {
            if(el.id === +id){
                group = el;
            }
        })
        this.setState(
            {
                openForm: true,
                modalAction: 'updterm',
                group: group,
            }
        );
    }

    render(){
        const groups = this.state.groups;
        const item = this.state.group;
        const lustUpdTime = this.state.upd;
        const table = groups.map((row) => 
                <TableRow
                    key={row.id}
                    row={row}
                    upd={this.upd}
                    displayUpdModal={this.displayUpdModal}
                />)
        const fields = [
            {
                editable: true,
                required: true,
                label: '',
                type: 'text',
                col: 12,
                fieldname: 'title',
                caption: ' 30 ',
                init: item ? item.name : ''
            },
            {
                editable: true,
                required: false,
                label: '',
                type: 'textarea',
                col: 12,
                fieldname: 'comment',
                caption: ' 300 ',
                init: item ? item.comment : ''
            },
            {
                editable: false,
                required: false,
                label: '',
                type: 'text',
                col: 6,
                fieldname: 'password',
                caption: ' ',
                init: item ? item.password : ''
            },
        ]
        const form = this.state.openForm
            && <ChForm
                type="modal"
                open={this.state.openForm}
                action={this.state.modalAction}
                close={this.closeModal}
                success={this.ifSuccess}
                fields={fields}
                item={item}
                />
        return(
            <div id="groups-tab" className="main-tab">
                <div className="tab_header display-flex justify-between mb1">
                    <small className="text-muted">upd: { lustUpdTime }</small>
                    <button
                        onClick={this.displayModal}
                        className="btn btn-outline-primary btn-sm"
                    > </button>
                </div>
                <table className="table table-hover">
                    <thead>
                        <tr className="table-dark">
                            <th>ID</th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {table}
                    </tbody>
                </table>
                
                {form}
                
                <Notice status={this.state.notice} />
            </div>
        )
    }
}

class TableRow extends React.Component{
    render(){
        const g = this.props.row;
        return(
            <tr>
                <td>{g.id}</td>
                <td>{g.name}</td>
                <td>{g.count}</td>
                <td style={{maxWidth: '100px', lineHeight: 1}}><small>{g.comment}</small></td>
                {/* <td>{g.options}</td> */}
                <td>
                    <RowOptions id={g.id}
                        upd={this.props.upd}
                        displayUpdModal={this.props.displayUpdModal}
                    />
                </td>
            </tr>
        )
    }
}

class RowOptions extends React.Component{

    state = {
        notice: null
    }

    removeTerm = (e) => {
        const m = e.target.closest('.btn');
        const termid = m.dataset.id;
        this.setState({notice: 'loading'});
        let formData = new FormData();
        formData.append('user', user.id);
        formData.append('action', 'delete_term');
        formData.append('termid', termid);
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    if(res.message === 200){
                        this.props.upd();
                        this.setState({notice: null});
                    }
                },
                (error) => {
                    this.setState({
                        error: error + ':   ',
                        notice: 'error'
                    });
                }
            )    
    }
    render(){
        return(
            <React.Fragment>
                <button
                    onClick={this.props.displayUpdModal}
                    data-id={this.props.id}
                    type="button"
                    className="btn btn-outline-info mr2"
                    data-toggle="tooltip"
                    data-placement="top"
                    title=" "
                    data-original-title=" "
                >
                    <i className="fas fa-cog"></i>
                </button>
                <button
                    onClick={this.removeTerm}
                    data-id={this.props.id}
                    data-action="delete_term"
                    type="button"
                    className="btn btn-outline-danger mr2"
                    data-toggle="tooltip"
                    data-placement="top"
                    title=" "
                    aria-describedby="tooltip738184"
                    data-original-title=" "
                >
                    <i  className="far fa-times-circle"></i>
                </button>
                <Notice status={this.state.notice} /> 
            </React.Fragment>
        )
    }
}

export { GroupsTab }