import React from 'react'
import {SocialIcons} from '../../social-icons/icons'
import './style.sass'

class Home extends React.Component {
  state = {
    pass: 'DEMO1',
    passForm: '',
    loginForm: 'hidden',
    userLogin: 'demo@mail.demo',
    userPass: 'demopassword',
  }

  handleChange = (e) => { //  
    const value = e.currentTarget.value
    this.setState({pass: value})
  }

  checkPass = (e) => { //   
    e.preventDefault()
    this.props.setCollCodeFunc(this.state.pass)
  }

  checkLogin = (e) => {
    e.preventDefault()
    let formData = new FormData();
    formData.append('userpass', this.state.userPass);
    formData.append('login', this.state.userLogin);
    formData.append('action', 'auth');
    fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
        .then(res => res.json())
        .then(
            (res) => {
                if( res['result'] === 'error' ){
                    this.setState({ error: '   ' });
                }
                else{
                    this.props.setAuthInfoFunc(res['message']);
                }
            },
            // :     ,     catch(),
            //         .
            (error) => {
            this.setState({
                error: error + ':   '
            });
            }
        )
  }

  userLoginHandleChange = (e) => {
    const value = e.currentTarget.value
    this.setState({userLogin: value})
  }
  userPassHandleChange = (e) => {
    const value = e.currentTarget.value
    this.setState({userPass: value})
  }
  toggleLoginForm = () => {
        if(this.state.passForm === 'hidden'){
            this.setState({ loginForm: 'hidden', passForm: '' })
        }
        else{
            this.setState({ loginForm: '', passForm: 'hidden' })
        }
  }

  render() {
    return (
        <section id="home">
            <div className="slogan">u can <span>CHOOSE</span> it...</div>
            <div className="home__right">
                <LoginBtn auth={this.state.auth} onclickFunc={this.toggleLoginForm} />
                <div className="home__right-content">
                    
                    <form className={this.state.passForm} onSubmit={this.checkPass}>
                        <div className="form-group">

                            <input
                                onChange={this.handleChange}
                                maxLength="5"
                                minLength="5"
                                pattern="[abcdefghjklmonpqrstuvwxyzABCDEFGHJKLMONPQRSTUVWXYZ0123456789]{5}"
                                className="form-control form-control-lg shadow-up"
                                type="text"
                                placeholder=" "
                                defaultValue="demo1"
                            />

                            <Notice text={this.props.error}/>
                            
                            <p className="text-center">     . 
                                   ,      <a href="#master-catalog"> </a>.
                            </p>
                        </div>
                    </form>

                    <form className={this.state.loginForm} onSubmit={this.checkLogin}>
                        <div className="form-group">

                            <input
                                onChange={this.userLoginHandleChange}
                                className="form-control form-control-lg shadow-up"
                                type="text"
                                placeholder=""
                                defaultValue="demo@mail.demo"
                            />

                            <input
                                onChange={this.userPassHandleChange}
                                className="form-control form-control-lg shadow-up"
                                type="password"
                                placeholder=""
                                defaultValue="demopassword"
                            />

                            <input
                                type="submit"
                                className="form-control form-control-lg shadow-up"
                                value="GO"
                            />

                            <Notice text={this.props.error}/>
                            
                            <p className="text-center"></p>
                        </div>
                    </form>
                
                </div>
            </div>
            <footer>
                <div className="home-left__networks">
                    < SocialIcons />
                </div>
                <ul className="flex-center justify-between flex-column-md w100p">
                    {/* <li><span>Be-Original 2018-2020</span></li> */}
                    <li><a href="#about"> </a></li>
                    <li><a href="#policy"> </a></li>
                    <li><span>CHOOSE 2019-2020</span></li>
                </ul>
            </footer>
        </section>
    )
  }
}

export { Home }

function Notice(props){
    if( props.text !== null){
        return <p className="linear-notice--error">{props.text}</p>;
    }
    else{
        return '';
    }
}

class LoginBtn extends React.Component{

    constructor(props){
        super(props);
        this.state = {trigger: 0};
    }

    onClickHandle = () => {
        if(this.state.trigger === 0){
            this.setState({trigger: 1});
        }
        else{
            this.setState({trigger: 0});
        }
        this.props.onclickFunc();
    }

    render(){
        if(this.props.auth === 1){
            return(
                <p>Hello!</p>
            )
        }

        let icon = "";
        if( this.state.trigger === 1 ){
            icon = 'shadow-up-hover active';
        }
        else{
            icon = 'shadow-up-hover';
        }
        return(
            <div onClick={this.onClickHandle} id="login-button" className={icon}>
                <i className="fas fa-sign-in-alt"></i>
            </div>
        )
    }
}