// Get user data from session

class User{

    data = JSON.parse(sessionStorage.getItem('CH_user'));

    cookieChecker(){
        if( sessionStorage.getItem('CH_user') === null ){
            return false;
        }
        else{
            return JSON.parse(sessionStorage.getItem('CH_user'));
        }
    }

    getProp(propname){
        const data = this.cookieChecker();
        if( !data )
            return '';
        if(data.upd.length !== 0)
        {
            const upd = JSON.parse(data.upd);
            if(upd){
                return upd[propname];
            }
        }
        else{
            return '';
        }
    }

    addOrderItem(orderItem){
        let cart = sessionStorage.getItem('CH_cart');
        let item = [];
        for( let f in orderItem.formats){ 
            if(orderItem.formats[f].qty > 0) item.push(orderItem.formats[f]);
        }
        orderItem.formats = item;
        if(!cart || cart === undefined || cart === "undefined"){
            cart = [];
        }
        else{
            cart = JSON.parse(cart);
        }
        cart.push(orderItem);
        sessionStorage.setItem('CH_cart', JSON.stringify(cart));
    }

    getCart(){
        let cart = JSON.parse(sessionStorage.getItem('CH_cart'));
        if( cart !== '' ){
            return this.prepareCart(cart);
        }
        else{
            return false;
        }
    }

    prepareCart(data){
        let cart = {
            count: 0,
            total: 0,
            items: data,
        }
        for(let n in data){
            if( data[n].formats !== undefined ){
                for(let f in data[n].formats){
                    const qty = data[n].formats[f].qty;
                    cart.count += qty;
                    cart.total += qty * data[n].formats[f].cost;
                }
            }
        }
        return cart;
    }

    updateOrderItem(e){
        const [formatID,action,imgPath,cart] = [
            e.dataset.id,
            e.dataset.action,
            e.dataset.path,
            this.getCart()
        ];
        if(cart){
            for(let item in cart){
                let n = cart[item]; 
                for(let f in n){
                    const m = n[f];
                    if(imgPath === m.itemPath){
                        for(let k in m.formats){
                            if(formatID === m.formats[k].id){
                                let a = m.formats[k].qty;
                                if(action === 'add')
                                    m.formats[k].qty++
                                else if(action === 'remove')
                                    if(a === 0)
                                        break;
                                    else
                                        m.formats[k].qty--
                                else if(action === 'delete')
                                    m.formats.splice(k,1); 
                                
                                sessionStorage.setItem('CH_cart', JSON.stringify(cart.items));
                                break;
                            }
                        }
                    }
                }
            }
            return false;
        }
        return false;
    }

    clear(){
        sessionStorage.removeItem('CH_cart');
    }

    upd(){
        let data = new FormData();
        data.append('user', this.id);
        data.append('action', 'get_user');
        fetch("http://peremena.rf/api/", { method: 'POST', body: data })
            .then(res => res.json())    
            .then(
                (res) => {
                    if( res.result === 'error' ){
                        console.error('    ')
                    }
                    else{
                        sessionStorage.setItem('CH_user', JSON.stringify(res));
                    }
                    return true;
                },
                (error) => {
                    //....    
                }
            )
        return false
    }

    formatsList(formats){
        let output = [];
        for(let f in formats){
            let n = formats[f];
            output.push({
                id: n.id,
                name: n.fototype +' '+ n.size,
            })
        }
        return output;
    }

    get id(){
        const data = this.cookieChecker();
        if( !data )
            return '';
        return data.id;
    }
    get name(){
        const data = this.cookieChecker();
        if( !data )
            return '';
        return data.displayName;
    }
    get date(){
        const data = this.cookieChecker();
        if( !data )
            return '';
        return data.date;
    }
    get cartTotal(){
        const cart = this.getCart();
        if(cart)
            return cart.total;
        else
            return 0;
    }
    get cartCount(){
        const cart = this.getCart();
        if(cart)
            return cart.count;
        else
            return 0;
    }
    get formats(){
        const data = this.cookieChecker();
        if( !data )
            return '';
        return data.formats;
    }
}

export {User}