import React, { useRef, useLayoutEffect, useState } from "react";
import { Children } from "react";
import {accountTips} from '../components/account-tips';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { extend } from "jquery";



class Tips extends React.Component{
    state = {
        open: true,
        tip: '',
    }

    randomTip(){
        let tips = accountTips[this.props.index];
        if(tips !== undefined){
            let index = randomInteger( 0, tips.length )
            return tips[index];
        }
        else{
            return false;
        }
    }

    render(){
        const tip = this.randomTip();
        
        if(!this.state.open || !tip)
            return null;

        return(
            <div key={this.props.index} className="toast show" role="alert" aria-live="assertive" aria-atomic="true">
                <div className="toast-header">
                    <strong className="mr-auto">:</strong>
                    <small>{''}</small>
                    <button onClick={() => this.setState({open: false})} type="button" className="ml-2 mb-1 close" data-dismiss="toast" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="toast-body">
                    <p dangerouslySetInnerHTML={{__html: tip}} />
                </div>
            </div>
        )
    }
}

function randomInteger(min, max) {
  let rand = min + Math.random() * (max + 1 - min);
  return Math.floor(rand);
}

export {Tips}

class InlineTips extends React.Component{
    state = {
        open: false
    }

    render(){
        if(this.state.open){
            return(
                <div className="inline-tip card border-warning mt100 mxa">
                    <div className="card-header">{this.props.title}<button onClick={() => this.setState({open: false})} type="button" className="close" data-dismiss="alert">&times;</button></div>
                    <div className="card-body">
                        {this.props.children}
                    </div>
                </div>
            )
        }
        else{
            return(
                <div onClick={() => this.setState({open: true})} className="inline-tips-starter">
                    <FontAwesomeIcon icon="info-circle" />
                </div>
            )
        }
    }
}
export {InlineTips}