import React from 'react'
import {User} from '../../user'
import ReactTooltip from 'react-tooltip';
import {Notice} from '../../notice'

let user = new User();

class AccountTab extends React.Component{

    state = {
        notice: null,
        isLoading: false,
    }

    handleChange = () => {

    }

    handleSubmit = (e) => {
        e.preventDefault();
        
        if( this.state.isLoading ){
            console.error('')
            return;}
        
        let formData = new FormData(e.target);
        //   /
        let updData = {};
        for(let [name, value] of formData) {
            updData[name] = value;
        }
        let endData = new FormData();
        endData.append('user', user.id);
        endData.append('action', 'update_user');
        endData.append('upd', JSON.stringify(updData));
        this.setState(
            {notice: 'loading', isLoading: true}
        )
        fetch("http://peremena.rf/api/", { method: 'POST', body: endData })
            .then(res => res.json())    
            .then(
                (res) => {
                    if( res.result === 'error' ){
                        this.setState({
                            notice: 'error'
                        });
                    }
                    else{
                        user.upd() && this.setState({ upd: new Date().toLocaleString() })
                        this.setState(
                            { notice: null, isLoading: false }
                        );
                    }
                },
                (error) => {
                    this.setState({
                        notice: 'error'
                    });
                }
            )
    }

    render(){
        const alert = <div className="alert alert-inline alert-success"><h4 className="alert-heading"> !</h4><p className="mb-0">,  ,    .</p></div>
        
        return(
            <div id="groups-tab" className="main-tab">
                <ReactTooltip type="info" />
                <Notice status={this.state.notice} />

                { user.getProp('actdrivekey') === '' ? alert : null }

                <form onSubmit={this.handleSubmit}>
                    <div className="tab_header display-flex justify-between mb2">
                        <div>
                            <small>* -    <a href="#"></a></small><br />
                            <small>** -    <a href="#"></a>  <i data-tip="    "> </i> </small>
                        </div>
                        <button type="submit" className="btn btn-outline-primary" disabled={this.state.isLoading}></button>
                    </div>                
                    <div className="row">
                        <div className="col-lg-4">
                            <Card title=" ">
                                <Input label=" /  *"
                                        caption="    ">
                                    <input
                                        className="form-control form-control-sm"
                                        type='text'
                                        name='username'
                                        defaultValue={user && user.getProp('username')}
                                        required
                                    />
                                </Input>
                                <Input label="  *"
                                        caption="   .     .">
                                    <textarea
                                        className="form-control form-control-sm"
                                        name='bio'
                                        defaultValue={user && user.getProp('bio')}
                                        placeholder="    "
                                        rows="4"
                                        required
                                    >
                                    </textarea>
                                </Input>
                            </Card>
                        </div>
                        <div className="col-lg-4">
                            <Card title=" **">
                                <Input label=""
                                       caption="">
                                    <div className="input-group mb-3">
                                        <div className="input-group-prepend">
                                            <span className="input-group-text">+7</span>
                                        </div>
                                        <input
                                            className="form-control form-control-sm"
                                            type='tel'
                                            name='tel'
                                            required
                                            defaultValue={user && user.getProp('tel')}
                                        />
                                    </div>
                                    
                                </Input>
                                <Input label=" "
                                       caption="">
                                    <input
                                        className="form-control form-control-sm"
                                        type='text'
                                        name='email'
                                        defaultValue={user && user.getProp('email')}
                                    />
                                </Input>
                            </Card>
                        </div>
                        <div className="col-lg-4">
                            <Card title="" 
                                    data={user ? user.getProp('actdrivekey') : 0}
                                    errormess="        ."
                                    >
                                <Input label=" "
                                        caption="       ">
                                    <select
                                        className="form-control form-control-sm"
                                        name='userdrive'
                                        defaultValue={user && user.getProp('userdrive')}
                                    >
                                        <option value="yd">.</option>
                                        {/* <option value="gd">Google </option> */}
                                    </select>
                                </Input>
                                <Input label=" "
                                        caption="     ">
                                    <input
                                        className="form-control form-control-sm"
                                        type='text'
                                        name='actdrivekey'
                                        defaultValue={user && user.getProp('actdrivekey')}
                                    />
                                </Input>
                                <h6>   :</h6>
                                <ol>
                                    <li className="">
                                        ,           ,       .
                                    </li>
                                    <li className="">   <a rel="noreferrer" target="_blank" href={authUrl()}>  </a>       .</li>
                                </ol>
                            </Card>
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}

export { AccountTab }

function  Input(props) {
    return(
        <div className="form-group">
            <label className="col-form-label col-form-label-sm">{props.label}</label>
            {props.children}
            <small className="form-text text-muted">{props.caption}</small>
        </div>
    )
}

function Card(props) {
    let error = false;
    if( props.data === 0 ){
        error = <ErrorIcon mess={props.errormess} />
    }
    return(
        <div className="card border-secondary mb3">
            <div className="card-header">{props.title} {error}</div>
            <div className="card-body">
                <div className="row">
                    <div className="col-lg-12">{props.children}</div>
                </div>
            </div>
        </div>
    )
}

function ErrorIcon(props) {
    return(
        <div className="float-right" data-tip={props.mess}>
            <i className="fas fa-exclamation-triangle text-warning fons120"></i>
        </div>
    )
}

function authUrl(params) {
    let url = 'https://oauth.yandex.ru/authorize?'
        url += 'response_type=token'
        url += '&client_id=159527890aee42b699bc57022fb0afbc'
        //[& device_id=< >]
        //[& device_name=< >]
        url += '&redirect_uri=https://oauth.yandex.ru/verification_code'
        //[& login_hint=<    >]
        //[& scope=<  >]
        //[& optional_scope=<  >]
        url += '&force_confirm=yes'
        //[& state=< >]
        url += '&display=popup'
    return url
}