import React from 'react'
import {User} from '../../user'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const u = new User();

class DriveTab extends React.Component{

    state = {
        path: 'app:/',
        sources: false,
        lvl: 1,
    }

    componentDidMount(){
        this.upd();
    }

    upd = (path = false) => {
        let formData = new FormData();
        formData.append('user', u.id);
        formData.append('action', 'get_resourses');
        formData.append('path', path ? path : this.state.path);
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    if( res.result === 'error' ){
                        this.setState({
                            notice: 'error'
                        });
                    }
                    else{
                        this.setState(
                            { 
                                sources: JSON.parse(res['message']),
                                upd: new Date().toLocaleString(),
                                notice: null
                            }
                        )
                    }
                },
                (error) => {
                    this.setState({
                        notice: 'error'
                    });
                }
            )
    }

    goToDir = (path) => {
        this.setState({
            path: this.preparePath(path),
            lvl: this.state.lvl + 1,
        });
        this.upd(path);
    }

    goBack = () => {
        let [path, exclude, output] = [this.state.path.split('/'), '', 'app:/'];
        exclude = path.pop();
        if( path.length > 1 ){    
            output = path.join('/');
        }
        else{
            output = 'app:/';
        }
        this.setState({
            path: output,
            lvl: this.state.lvl > 1 ? this.state.lvl - 1 : 1,
        });
        this.upd(output);
    }

    preparePath(path){
        return path.replace( 'disk://CHOOSE/', 'app:/' );
    }

    render(){
        let dirs = []
        if(this.state.sources){
            for(let dir in this.state.sources){
                dirs.push(<Src path={this.state.path} data={this.state.sources[dir]} goTo={this.goToDir} />);
            }
            return(
                <>
                <div className="d-flex justify-between">
                    <div className="breadcrumbs"><FontAwesomeIcon icon="arrow-alt-circle-right" className="mr10" /> {this.state.path}</div>
                    <DirLvl_Caption lvl={this.state.lvl} />
                </div>
                <table className="table table-hover">
                    <thead>
                        <tr className="table-primary">
                            <td style={{width:'50px'}}></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </thead>
                    <tbody>
                        <BackBtn f={this.goBack} path={this.state.path} />
                        {dirs}
                    </tbody>
                </table>
                </>
            )
        }
        else{
            return null;
        }
        
    }
}

export {DriveTab}

function BackBtn(props){
    if( props.path !== 'app:/' ){
        return(
            <tr><td>
                <button
                    key={props.path}
                    onClick={props.f}
                    className="btn btn-outline-info btn-sm">
                        <FontAwesomeIcon icon="long-arrow-alt-left" className="src-icon" />
                </button>
            </td></tr>
        )
    }
    else{
        return <tr><td>...</td></tr>;
    }
}

function Src(props){
    let [icon, onclick] = ['file', () => {}];
    if(props.data.type === 'dir'){
        icon = 'folder';
        onclick = () => props.goTo(props.data.path);
    }
    
    return(
        <tr className="drive__dir" onClick={onclick}>
            <td><FontAwesomeIcon icon={icon} className="src-icon text-info" /></td>
            <td className="tal"><span className="title">{props.data.name}</span></td>
            <td><span className="path"></span></td>
        </tr>
    )
}

function DirLvl_Caption(props){
    let text = '';
    if(props.lvl === 1){
        text = <div className="d-flex"><small className="mr10">  </small><h5></h5></div>;
    }
    else if(props.lvl === 2){
        text = <div className="d-flex"><small className="mr10">  </small><h5></h5></div>;
    }
    else if(props.lvl === 3){
        text = <div className="d-flex"><small className="mr10">  </small><h5> </h5></div>;
    }
    else if(props.lvl === 4){
        text = <div className="d-flex"><small className="mr10">  </small><h5></h5></div>;
    }
    return text;
}