import React from 'react'

class Notice extends React.Component{
    
    state = {
        status: this.props.status !== 0 ? this.props.status : 0,
    }

    handleClose = () => {
        this.setState({status: 0});
    }

    componentDidMount(){
        if( this.state.status === 'success' || this.state.status === 'error' ){
            this.timer = setTimeout(() => {
                this.setState({status: 0});
            }, 2000)
        }
    }

    componentWillUnmount(){
        clearTimeout(this.timer);
    }

    render(){
        //console.log(this.props.status)
        let status = this.props.status ? this.props.status : this.state.status;
        if(this.state.status === 0){
            return null
        }

        if(status === 'loading'){
            return(
                <div className="alert alert-dismissible alert-info">
                    <button onClick={this.handleClose} type="button" className="close" data-dismiss="alert">&times;</button>
                    <strong>...</strong>
                </div>
            )
        }
        else if(status === 'success'){
            return(    
                <div className="alert alert-dismissible alert-success">
                    <button onClick={this.handleClose} type="button" className="close" data-dismiss="alert">&times;</button>
                    <strong>!</strong>
                </div>
            )
        }
        else if(status === 'error'){
            return(    
                <div className="alert alert-dismissible alert-danger">
                    <button onClick={this.handleClose} type="button" className="close" data-dismiss="alert">&times;</button>
                    <strong>!</strong>
                </div>
            )
        }
        else{
            return null
        }
    }
}

export { Notice }