import React from 'react';
import 'bootswatch/dist/superhero/bootstrap.min.css';
//import $ from 'jquery';
//import Popper from 'popper.js';
import './App.sass';
import './animate.sass';
import '@fortawesome/fontawesome-free/js/all';
// Components
import {Home} from './components/pages/home/home';
import {Collection} from './components/pages/collection/collection';
import {Account} from './components/pages/account/account';

class App extends React.Component {

  state = {
    page: 'home',
    collPass: false,
    error: null,
    auth: 0,
    post: null,
  }

  setCollCode = (pass) => {
    this.setState({collPass: pass, error: null})
    let formData = new FormData();
    formData.append('password', pass);
    formData.append('action', 'check_session_password');
    fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
      .then(res => res.json())
      .then(
        (res) => {
          if( res.result !== 'error' ){
            this.setState({ page: 'collection', post: res });
          }
          else{
            this.setState({ error: ' ' });
          }
        },
        // :     ,     catch(),
        //         .
        (error) => {
          this.setState({
            error: error + ':   '
          });
        }
    )
  }

  setAuthInfoFunc = (data) => {
    sessionStorage.setItem('CH_user', JSON.stringify(data));
    setTimeout(() => this.setState({page: 'account'}), 1000);
  }

  setPage = (title) => {
    this.setState({page: title})
  }

  render(){
    if(this.state.page === 'home')
      return  <Home
                error={this.state.error}
                setPageFunc={this.setPage}
                setCollCodeFunc={this.setCollCode}
                setAuthInfoFunc={this.setAuthInfoFunc}
              />
    
    if(this.state.page === 'collection')
      return <Collection setPageFunc={this.setPage} post={this.state.post} />

    if(this.state.page === 'account')
      return <Account setPageFunc={this.setPage} userData={this.state.auth} />
  }
  
}

export default App;
