import React, { useRef, useLayoutEffect, useState } from "react";
import { Notice } from '../notice'
import { User } from '../user'
//import { Formik, Form, Field, ErrorMessage } from 'formik';
// import { faShieldVirus } from "@fortawesome/free-solid-svg-icons";

const user = new User();

function Modal(props){
    
    const targetRef = useRef();
    const [dimensions, setDimensions] = useState({ top:0, left: 0 });

    // useLayoutEffect(() => {
    //     if (targetRef.current) {
    //         const ww = window.innerWidth,
    //               mw = targetRef.current.closest('#main').offsetWidth;
    //         setDimensions({
    //             top: 100,
    //             left: (ww - mw) / 2,
    //         });
    //     }
    // }, []);
    if( !props.open ){
        return null
    }
    return(
        <div className="modal open">
            <div className="modal-dialog" ref={targetRef} role="document"
                 style={{top: dimensions.top, left: dimensions.left}}>
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title">{props.title}</h5>
                        <button onClick={props.close} type="button" className="close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        {props.children}
                    </div>
                    {props.footer && <div className="modal-footer">props.footer</div>}
                </div>
            </div>
        </div>
    )
}

export { Modal }

class ChForm extends React.Component{

    state = {
        notice: ''
    }

    handleChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({
          [name]: value
        });
    }

    submit = (e) => {
        e.preventDefault();
        // Create html query
        let formData = new FormData(e.target);
        formData.append('user', user.id);
        formData.append('action', this.props.action);
        if(this.props.action === 'updpost' || this.props.action === 'updterm' || this.props.action === 'updformat')
            formData.append('id', this.props.item !== null ? this.props.item.id : 0)
        // Display loading notice
        this.setState({notice: 'loading'});
        // Create Fetch
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())
            .then(
                (res) => {
                    this.props.success()
                },
                (error) => {
                    this.setState({
                        notice: 'error'
                    });
                    console.log(error)
                }
            )
    }

    render(){
        const initialValues = {};
        this.props && this.props.fields.forEach((el,i) => {
            initialValues[el.name] = el.init !== null ? el.init : '';
        })
        const form =
            <div className="row">
                <form className="col-12" onSubmit={this.submit}>
                    <div className="row">
                    {
                        this.props.fields.map((el) =>
                            <div className={"form-group col-" + el.col} key={el.fieldname}>
                                <label htmlFor={el.fieldname}>{el.label}</label>
                                <Input el={el} onChangeFunc={this.handleChange} item={this.props.item}/>
                                <small className="form-text text-muted">{el.caption}</small>
                            </div>
                        )
                    }
                        <div className="col-12">
                            <button type="submit" disabled={this.state.isSubmitting} className="btn btn-primary btn-block">
                                
                            </button>
                        </div>
                    </div>
                </form>
            </div>
                
        
        if( this.props.type === 'modal' ){
            return(
                <React.Fragment>
                    <Modal
                        open={this.props.open}
                        title={getTitle( this.props.action )}
                        close={this.props.close}>
                            {form}
                    </Modal>
                    <Notice status={this.state.notice} />
                </React.Fragment>
            )
        }

        return('')
    }
}

export { ChForm }

function getTitle(action){
    if( action === 'addterm' ){
        return ' '
    }
    else if( action === 'updterm' ){
        return ' '
    }
    else if( action === 'addpost' ){
        return ' '
    }
    else if( action === 'updpost' ){
        return ' '
    }
    else if( action === 'updformat' ){
        return ' '
    }
    else if( action === 'addformat' ){
        return ' '
    }
    else{
        return 'Undefined';
    }
}

function Input(props) {
    const el = props.el;
    if(el.type === 'select'){
        let options = el.options.map((option) =>
            <option key={option.id} value={option.id}>{option.name}</option>
        )
        return(
            <select
                onChange={props.handleChange}
                disabled={!el.editable}
                required={el.required}
                className="form-control"
                multiple={el.multiple}
                type={el.type}
                as={el.type}
                name={el.fieldname}
                defaultValue={el.init}
            >
            {options}
            </select>)
    }
    else if(el.type === 'checkbox'){
        return(
            <input
                onChange={props.handleChange}
                disabled={!el.editable}
                required={el.required}
                className="form-control"
                type={el.type}
                name={el.fieldname}
                defaultChecked={el.init}
            />
        )
    }
    else if(el.type === 'file'){
        return(
            <input
                onChange={props.handleChange}
                disabled={!el.editable}
                required={el.required}
                className="form-control"
                type={el.type}
                name={el.fieldname}
            />
        )
    }
    else if(el.type === 'textarea'){
        return(
            <textarea
                onChange={props.handleChange}
                disabled={!el.editable}
                required={el.required}
                className="form-control"
                rows="4"
                as={el.type}
                name={el.fieldname}>
            </textarea>
        )
    }
    else if(el.type === 'formatsList'){
        return(<FormatsList formatsChanged={props.item && props.item.formats} />)
    }
    else{
        return(
            <input
                onChange={props.handleChange}
                disabled={!el.editable}
                required={el.required}
                className="form-control"
                type={el.type}
                name={el.fieldname}
                defaultValue={el.init}
            />  
        )
    }
}

class FormatsList extends React.Component{

    state = {
        formats: []
    }

    componentDidMount(){
        let formData = new FormData();
        formData.append('user', user.id);
        formData.append('action', 'getformats');
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())    
            .then(
                (res) => {
                    this.setState({
                        formats: res
                    })
                },
                (error) => {
                    this.setState({
                        formats: []
                    })
                }
            )
    }

    isChecked = (id) => {
        let is = false;
        this.props.formatsChanged && this.props.formatsChanged.forEach(el => {
            if(el == id) is = true; 
        })
        return is;
    }

    render(){
        const formats = user.formatsList(this.state.formats).map(f =>
            <div key={f.id} className="form-check">
                <input
                    id={f.id}
                    className="form-check-input"
                    type="checkbox"
                    name="format[]"
                    value={f.id}
                    defaultChecked={this.isChecked(f.id)}
                />
                {f.name}
            </div>
        )
        if(formats.length === 0){
            return(
                <div class="alert alert-dismissible alert-danger">
                <strong>!</strong>          .
                </div>
            )
        }
        return(
            <div className="formats-list">
                <p></p>
                <div className="formats-list__wrapper">
                    {formats}
                </div>
            </div>
        )
    }
}