import React from 'react'
import {GroupsTab} from './groups-tab'
import {CollectionTab} from './collections-tab'
import {FormatsTab} from './formats-tab'
import {OrdersTab} from './orders-tab'
import {AccountTab} from './account-tab'
import {DriveTab} from './drive-tab'

import './style.sass'
import {User} from '../../user'
import { Tips } from '../../helper'

const user = new User();

class Account extends React.Component{

    state = {
        mainTab: user.getProp('actdrivekey') === '' ? 'account' : 'disk',
        login: user.cookieChecker()
    }

    swapMainTabs = (e) => {
        if(!user.cookieChecker()){
            this.props.setPageFunc('home')
        }
        if(user.getProp('actdrivekey') === ''){
            return
        }
        this.setState({ mainTab: e.target.closest('li').dataset.tab })
    }

    componentDidMount(){
        
    }

    componentWillUnmount(){
        if(!user.cookieChecker()){
            
        }
    }

    render(){
        //console.log(this.props.userData)
        const mainMenu = [
            {
                key: 'disk',
                title: '',
                mess: 0,
                icon: 'fas fa-hdd',
                current: this.state.mainTab === 'disk' ? true : false,
            },
            {
                key: 'groups',
                title: '',
                mess: 0,
                icon: 'fas fa-object-group',
                current: this.state.mainTab === 'groups' ? true : false,
            },
            {
                key: 'collections',
                title: '',
                mess: 0,
                icon: 'fas fa-layer-group',
                current: this.state.mainTab === 'collections' ? true : false,
            },
            {
                key: 'formats',
                title: '',
                mess: 0,
                icon: 'fas fa-tags',
                current: this.state.mainTab === 'formats' ? true : false,
            },
            {
                key: 'orders',
                title: '',
                mess: 0,
                icon: 'fas fa-shopping-bag',
                current: this.state.mainTab === 'orders' ? true : false,
            },
            {
                key: 'account',
                title: '',
                mess: 0,
                icon: 'fas fa-user-tie',
                current: this.state.mainTab === 'account' ? true : false,
            }
        ]

        return(
            <div id="account-wrapper">
                <Sidebar currentPage={this.state.mainTab} mainMenu={mainMenu} swapfunc={this.swapMainTabs} {...this.props} />
                <Main activeTab={this.state.mainTab} />
            </div>
        )
    }
}

export { Account }

class Sidebar extends React.Component{
    render(){
        return(
            <div id="sidebar">
                <div className="sidebar__body">
                    <ul className="mainmenu list-group">
                    <li onClick={() => this.props.setPageFunc('home')}
                        key="go-home-btn"
                        className="list-group-item d-flex justify-content-between align-items-center"
                    >
                        <span className="flex-column flex-center">
                            <i className="fas fa-home"></i>
                            <strong></strong>
                        </span>
                    </li>
                        <List mainMenu={this.props.mainMenu} swapfunc={this.props.swapfunc} />
                    </ul>
                </div>
                <div className="sidebar__footer">
                    {/* <Tips index={this.props.currentPage} /> */}
                </div>
            </div>
        )
    }
}

class Main extends React.Component{
    
    render(){
        let tab = <GroupsTab /> //  

        if( this.props.activeTab === 'disk' ){
            tab = <DriveTab />
        }
        else if( this.props.activeTab === 'collections' ){
            tab = <CollectionTab />
        }
        else if( this.props.activeTab === 'orders' ){
            tab = <OrdersTab />
        }
        else if( this.props.activeTab === 'formats' ){
            tab = <FormatsTab />
        }
        else if( this.props.activeTab === 'orders' ){
            tab = <OrdersTab />
        }
        else if( this.props.activeTab === 'account' ){
            tab = <AccountTab />
        }
        return(
            <div id="main">
                <div className="main__header navbar-dark bg-dark">
                    <div id="logo" onClick={() => this.props.setPageFunc('home')} className="navbar-brand btn">CHOOSE</div>
                    <span className="mla">, {user.name}!</span>
                </div>
                <div className="main__body">
                    {tab}
                </div>
            </div>
        )
    }
}

function List(props) {
    const numbers = props.mainMenu;
    const listItems = numbers.map((number) => 
        <li onClick={props.swapfunc} 
            key={number.key.toString()}
            data-tab={number.key}
            data-current={number.current}
            className="list-group-item d-flex justify-content-between align-items-center"
        >
            <span className="flex-column flex-center">
                <i className={number.icon}></i>
                <strong>{number.title}</strong>
            </span>
            <Badge count={number.mess} />
        </li>
    );
    return listItems;
}

function Badge(props){
    if(props.count > 0)
        return <div className="badge__wrapper"><span className="badge badge-primary badge-pill">{props.count}</span></div>
    else
        return null
}