import React from 'react'
import './style.sass'
import Img from '../../../images/demo.png'
import {Notice} from '../../notice'
import {User} from '../../user'

const u = new User();

class Image extends React.Component{

    state = {
        size: 'vert',
        isCurrent: this.props.currentSteck,
        orderItem: {
            itemTitle: this.props.item[0],
            itemPath: this.props.item[1],
            formats: this.props.f
        },
    }

    handleDoImageFormat = (e) => {
        const id = e.target.dataset.id;
        const action = e.target.dataset.action;
        let f = this.state.orderItem;
        if( action === 'add'){
            f.formats[id].qty++;
            this.setState({orderItem: f});
        }
        else{
            f.formats[id].qty > 0 && f.formats[id].qty--;
            this.setState({orderItem: f});
        }
    }

    handleChange = (e) => {
        if(this.props.currentSteck){
            this.setState({
                isCurrent: true
            })
            this.props.setStepFunc(3);
            for( let f in this.state.orderItem.formats ){
                let j = this.state.orderItem;
                j.formats[f].qty = 0;
                this.setState({orderItem: j})
            }
        }
    }

    handleRemove = () => {
        this.setState({
            isCurrent: false
        })
        this.props.setStepFunc(2);
    }

    handleAddToCart = () => {
        u.addOrderItem(this.state.orderItem);
        this.setState(
            {
                orderItem: {
                    itemTitle: this.props.item[0],
                    itemPath: this.props.item[1],
                    formats: this.props.f
                },
                isCurrent: false
            }
        );
    }

    autoResize = (e) => {
        let w = e.target.offsetWidth;
        let h = e.target.offsetHeight;
        if(w > h){
            //e.target.closest('.foto-group__img').classList.add('horz');
            //this.setState({size: 'horz'})
            //e.target.closest('.foto-group__img-wrapper').style.paddingTop = '200px';
        }
    }

    componentDidUpdate(){
        if(!this.props.currentSteck && this.state.isCurrent)
            this.setState({ isCurrent: false });
    }

    render(){
        const displayFormats = this.state.isCurrent && this.props.currentSteck ? true : false;
        return(
            <>
                <img
                    onClick={this.handleChange}
                    onLoad={this.autoResize}
                    className="yd-foto loading"
                    data-current={this.state.isCurrent}
                    data-path={this.props.item[1]}
                    data-title={this.props.item[0]}
                    src={Img}
                    alt="choose.ru"
                />
                { displayFormats && <Formats
                                        remove={this.handleRemove}
                                        func={this.handleDoImageFormat}
                                        addtocart={this.handleAddToCart}
                                        formats={this.state.orderItem.formats}
                                        { ...this.props }
                                    />
                }
            </>
        )
    }
}

function FormatItem(props){
    const f = props.el;
    return(
        <div className="formats__item-wrapper">
            <div data-id={f.id}
                    data-action="remove"
                    onClick={props.func}
                    className="qty_mod qty_mod--remove">-</div>
            <div className="formats__item">
                <span className="formats__title">{f.type}</span>
                <span className="formats__size">{f.size} ()</span>
                <span className="formats__cost">
                    {f.cost}.00 &nbsp;<span className="qty">x{f.qty}</span>
                </span>
            </div>
            <div data-id={f.id}
                    data-action="add"
                    onClick={props.func}
                    className="qty_mod qty_mod--add">+</div>
        </div>
    )
}

class Formats extends React.Component{
    
    componentDidMount(){
        document.body.style.overflow = 'hidden';
    }

    componentWillUnmount(){
        document.body.style.overflow = 'auto';
    }
    
    render(){
        let tpl = [];
        const f = this.props.f;
        let itemTotal = 0;
        let itemTotalCount = 0;
        let cartBtn = null;
        for( let i in f ){
            itemTotal += f[i].cost*f[i].qty;
            itemTotalCount += f[i].qty;
            tpl.push(<FormatItem key={i} func={this.props.func} el={f[i]} />)
        }
        if( itemTotal > 0 ){
            this.props.setStepFunc(4);
            cartBtn = <div onClick={this.props.addtocart} className="flex-center flex-column justify-center item-total__cart-icon">
                        <i className="fas fa-cart-plus mb5"></i>
                        <small>  </small>
                      </div>
        }else
            this.props.setStepFunc(3);
        return (
            <div id="formats">
                <div className="formats-wrapper">
                    <div className="formats__item item-total d-flex">
                        <div className="flex-column item-total__wrapper">
                            <div className="d-flex justify-between">
                                <small>:&nbsp;</small>{itemTotalCount}
                            </div>
                            <div className="flex-center justify-between">
                                <small> :&nbsp;</small>
                                <span>{itemTotal}&nbsp;<i className="fas fa-ruble-sign fons80"></i></span>
                            </div>
                        </div>
                        {cartBtn}
                    </div>
                    {tpl}
                    <div onClick={this.props.remove} className="formats__item cancel"></div>
                </div>
            </div>
        )
    }
}

class FotoGroupsItem extends React.Component{
    state = {
        currentSteck: false,
        step: 2,
    }
    
    handleMouseEnter = (e) => {
        e.target.closest('.foto-group__img').classList.add('zoomed')
    }
    handleMouseLeave = (e) => {
        e.target.closest('.foto-group__img').classList.remove('zoomed')
    }
    handleOpen = (e) => {
        this.setState({currentSteck: true});
    }
    toggleCurrentSteck = (e) => {
        this.setState({ currentSteck: false });
        this.setStep(2);
    }
    componentWillUnmount(){
        this.setState({
            changedImagePath: null,
            changedImageTitle: null,
        })
    }
    setStep = (n) => {
        if(n !== this.state.step){
            let num = typeof(n) === 'number' ? n : n.target.dataset.updstep;
            this.setState({ step: num })
        }
    }

    render(){
        let tpl = [];
        this.props.fotos.reverse().forEach((el,i) => {
            tpl.push(<Image
                        key={i}
                        item={el}
                        currentSteck={this.state.currentSteck}
                        setStepFunc={this.setStep}
                        f={this.props.f}
                    />)
        })
        if( this.state.currentSteck ){
            return(
                <>
                    <div className="foto-group__img current-item" data-stacked={this.props.stacked}>
                        <div className="foto-group__img-wrapper">
                            {tpl}
                        </div>
                    </div>
                    <div className="top-bar-optional">
                        <div
                            className="flex-center flex-column top-bar__item"
                            data-updstep="2"
                            onClick={this.toggleCurrentSteck}
                        >
                            <button className="btn btn-outline-info">
                                <i className="fas fa-arrow-left"></i>
                            </button>
                            <span></span>
                        </div>
                        <div className="bar__notice flex-center">
                            <DisplayStepCaption n={this.state.step} />
                        </div>
                    </div>
                </>
            )
        }
        else{
            return(
                <div className="foto-group__img" data-stacked={this.props.stacked}>
                    <div className="foto-group__img-wrapper">
                        {tpl}
                    </div>
                    <button
                        onMouseEnter={this.handleMouseEnter}
                        onMouseLeave={this.handleMouseLeave}
                        className="zoom-foto"><i className="fas fa-eye"></i>
                    </button>
                    <button
                        onClick={this.handleOpen}
                        className="open-foto-options-btn"><i className="fas fa-layer-group"></i>
                    </button>
                </div>
            )
        }
    }
}

function DisplayStepCaption(props){
    if(props.n === 2)
        return (<span className=""> </span>);
    else if(props.n === 3)
        return (<span className=""> </span>);
    else if(props.n === 4)
        return (<span className="">  </span>);
    else
        return null;
}

function FotoGroup(props){
    let tpl = [];
    let stack = [];
    let m = 3;
    for( let i = 1 ; i <= props.items.length; i++ ){
        stack.push(props.items[i-1]);
        if( props.items.length < m ){
            tpl.push( <FotoGroupsItem key={i} fotos={stack} stacked="0" f={props.f} /> );
            stack = [];
        }
        else{
            if( i%m && i!==0 ){
                
            }
            else{
                tpl.push( <FotoGroupsItem key={i} fotos={stack} stacked="1" f={props.f} /> );
                stack = [];
            }
        }
        
    }

    return(
        <div className="foto-group">
            <div className="foto-group__title">{props.title}</div>
            <div className="foto-group__wrapper">
                {tpl}
            </div>
        </div>
    )
}

function FotoGroups(props){
    
    if(props.drive === null)
        return('');
    
    let tpl = [];
    for( let group in props.drive.groups){
        let g = props.drive.groups[group];
        tpl.push(<FotoGroup key={group} title={group} items={g} f={props.f} />);
    }
    
    return(
        <div id="foto-groups">
            {tpl}
        </div>
    )
}

class Collection extends React.Component {
    state = {
        drive: null
    }

    componentDidMount() {
        u.clear();
        if(this.state.drive === null){
            //    
            let formData = new FormData();
            formData.append('user', this.props.post.post_author);
            formData.append('session', this.props.post.ID);
            formData.append('action', 'getgroups');
            fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
                .then(res => res.json())
                .then(
                    (res) => {
                        if( res['result'] === 'error' ){
                            this.setState({ notice: 'error' })
                        }
                        else if( res['result'] === 'ok' ){
                            this.setState({
                                drive: JSON.parse(res['message']),
                                notice: null
                            });
                        }
                        else{
                            this.setState({ notice: 'error' })
                        }
                    },
                    (error) => {
                        this.setState({
                            notice: 'error'
                        });
                    }
                )
            this.fotos = false;
            this.loading = setInterval(
                () => {
                    const fotos = document.querySelectorAll('.yd-foto.loading');
                    fotos.forEach((el,i) => {
                        this.visible(el)
                    })
                },
                1000
            );
        }
    }

    componentWillUnmount(){
        clearInterval(this.loading);
        this.fotos = false;
    }

    visible = function (target) {
        //   
        var targetPosition = {
            top: window.pageYOffset + target.getBoundingClientRect().top,
            left: window.pageXOffset + target.getBoundingClientRect().left,
            right: window.pageXOffset + target.getBoundingClientRect().right,
            bottom: window.pageYOffset + target.getBoundingClientRect().bottom
          },
          //   
          windowPosition = {
            top: window.pageYOffset,
            left: window.pageXOffset,
            right: window.pageXOffset + document.documentElement.clientWidth,
            bottom: window.pageYOffset + document.documentElement.clientHeight
          };
      
        if (targetPosition.bottom > windowPosition.top && //          ,    
          targetPosition.top < windowPosition.bottom && //          ,    
          targetPosition.right > windowPosition.left && //          ,    
          targetPosition.left < windowPosition.right) { //          ,    
          //    ,    
          //console.clear();
          this.uploadFoto(target, target.dataset.path);
        } else {
          //    ,    
          //console.clear();
        };
    };

    uploadFoto(target, path){
        target.classList.remove('loading');
        let formData = new FormData();
        formData.append('path', path);
        formData.append('user', this.props.post.post_author);
        formData.append('action', 'getpreview');
        fetch("http://peremena.rf/api/", { method: 'POST', body: formData })
            .then(res => res.json())
            .then(
                (res) => {
                    if( res['result'] === 'error' ){
                        //...
                    }
                    else if( res['result'] === 'ok' ){
                        target.src = res['message'];
                    }
                    else{
                        //...
                    }
                },
                (error) => {
                    this.setState({
                        notice: 'error'
                    });
                }
            ) 
    }

    goHome = () => {
        if(window.confirm('       .')){
            this.props.setPageFunc('home')
        }   
    }

    render(){
        const g = this.state.drive;
        return (
            <section id="coll" className="pb60">
                <Notice status={this.state.notice} />
                <div className="top-bar">
                    <div onClick={this.goHome} id="go-home" className="flex-center flex-column top-bar__item">
                        <button className="btn btn-outline-info">
                            <i class="fas fa-home"></i>
                        </button>
                        <span></span>
                    </div>
                    <div id="master-contacts" className="flex-center flex-column top-bar__item mra">
                        <button className="btn btn-outline-info">
                            <i className="far fa-address-book"></i>
                        </button>
                        <span></span>
                    </div>
                    <Cart sessionid={this.props.post.ID} author={this.props.post.post_author} />
                </div>
                <FotoGroups drive={g} f={this.props.post.formats} />
            </section>
        )
    }
}

export {Collection}

class Cart extends React.Component{
    state = {
        stage: 'close'
    }

    upd = () => {
        this.setState({ mod: new Date().getTime() })
    }

    setStage_afterRequest = () => {
        this.setState({
            stage: 'thankyou'
        })
    }

    setStage = (key) => {
        this.setState({
            stage: key.toString(),
        })
    }

    componentDidMount(){
        this.checkCartCount = setInterval(() => {
            this.setState({cartCount: u.cartCount});
        }, 500);
    }

    componentWillUnmount(){
        this.setState({
            stage: 'close',
        })
        clearInterval(this.checkCartCount);
    }

    render(){
        const cart = u.getCart();
        let cartItems = [];
        let displayItems = [];
        if(cart !== undefined){
            for( let item in cart.items ){
                let [n, path] = [cart.items[item], cart.items[item].itemPath ];
                if( cartItems[path] === undefined ){
                    cartItems[path] = [];
                }
                if( n !== undefined ){
                    for( let m in n.formats ){
                        cartItems[path].push( <CartItem key={m +'-'+ item}
                                                        path={path}
                                                        format={n.formats[m]}
                                                        upd={this.upd}
                                                />)
                    }
                }
            }
            //    
            for(let n in cartItems){
                displayItems.push( cartItems[n] );
            }    
        }
        
        const checkoutBtn = <div onClick={() => this.setState({stage: 'checkout'})}
                                 className="flex-center flex-column justify-center item-total__cart-icon">
                                {/* <i className="fas fa-money-check mb5"></i> */}
                                <small> </small>
                            </div>;
        
        if( this.state.stage === 'open' ){
            return(
                <div id="cart-widget-open" className="layer pb80">
                    {displayItems}
                    <div className="formats__item item-total d-flex mt30">
                        <div className="flex-column item-total__wrapper">
                            <div className="d-flex justify-between">
                                <i className="fas fa-portrait fons80"></i>&nbsp;<span >{u.cartCount}&nbsp;<small>.</small></span>
                            </div>
                            <div className="flex-center justify-between">
                                <i className="fas fa-coins fons80"></i>&nbsp;
                                <span>{u.cartTotal}&nbsp;<i className="fas fa-ruble-sign fons80"></i></span>
                            </div>
                        </div>
                        {u.cartTotal > 0 && checkoutBtn}
                    </div>
                    <Bar setstage={this.setStage} stage={this.state.stage} />
                </div>
            )
        }
        else if(this.state.stage === 'checkout' || this.state.stage === 'thankyou' || this.state.stage === 'error'){
            return(
                <div id="cart-widget-open" className="layer pb80">
                    <CheckoutForm
                        setStage={this.setStage}
                        sessionid={this.props.sessionid}
                        authorID={this.props.author}
                    />
                    <Bar setstage={this.setStage} stage={this.state.stage} />
                </div>
            )
        }
        else{
            return(
                <div onClick={() => this.setStage('open')} id="minicart-widget" className="flex-center flex-column top-bar__item">
                    <button className="btn btn-outline-info"><i className="fas fa-shopping-cart"></i></button>
                    <span></span>
                    <span className="badge badge-pill badge-primary">{this.state.cartCount}</span>
                </div>
            );
        }
    }
}

function Bar(props){
    let { setstage, btn, notice } = '';
    if(props.stage === 'open' && u.cartTotal > 0){
        setstage = 'close';
        notice = '     ';
        btn = '';
    }
    else if(props.stage === 'open' && u.cartTotal === 0){
        setstage = 'close';
        notice = ' ';
        btn = '';
    }
    else if(props.stage === 'checkout'){
        setstage = 'open';
        notice = '  ';
        btn = ' ';
    }
    else if(props.stage === 'thankyou'){
        setstage = 'close';
        btn = '';
        notice = '!';
    }
    else if(props.stage === 'error'){
        setstage = 'open';
        notice = ' !  .';
        btn = ' ';
    }

    return(
        <div className="top-bar">
            <div className="flex-center flex-column top-bar__item"
                onClick={() => props.setstage(setstage)}>
                <button className="btn btn-outline-info">
                    <i className="fas fa-arrow-left"></i>
                </button>
                <span>{btn}</span>
            </div>
            <div className="bar__notice flex-center">
                {notice}
            </div>
        </div>
    )
}

function CartItem(props){
    const f = props.format;
    return(
        <div className="cart-item__wrapper">
            <div className="cart-item__img">
                <img
                    className="yd-foto loading"
                    data-path={props.path}
                    data-title={''}
                    src={Img}
                    alt="choose.ru"
                />
            </div>
            <div className="cart-item__info">
                <span><small>:</small> {f.fototype}</span>
                <span><small>:</small> {f.size}</span>
                <span><small>:</small> {f.cost}.0</span>
                <span><small>-:</small> {f.qty}</span>
                <span><small>:</small> {f.cost*f.qty}.0 <i className="fons80 fas fa-ruble-sign"></i></span>
            </div>
            <div className="cart-item__controlls">
                <div className="formats__item-wrapper">
                    <div data-id={f.id}
                            data-action="remove"
                            data-path={props.path}
                            onClick={(e) => {u.updateOrderItem(e.target); props.upd()}}
                            className="qty_mod qty_mod--remove">-</div>
                    <div className="btn btn-outline-secondary btn-sm"
                         data-id={f.id}
                         data-action="delete"
                         data-path={props.path}
                         onClick={(e) => {u.updateOrderItem(e.target); props.upd()}}>
                          
                    </div>
                    <div data-id={f.id}
                            data-action="add"
                            data-path={props.path}
                            onClick={(e) => {u.updateOrderItem(e.target); props.upd()}}
                            className="qty_mod qty_mod--add">+</div>
                </div>
            </div>
        </div>
    )
}

class CheckoutForm extends React.Component{

    state = {
        formReady: false,
        addOrder_Result: false
    }

    handleChange = (e) => {
        const target = e.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
    
        this.setState({
          [name]: value
        });
        
        this.validateForm(e.target.closest('form'));
    }
    
    handleSubmit = (e) => {
        e.preventDefault();
        if(this.state.notice === 'loading'){
            return;
        }
        this.setState({ notice: 'loading' });
        fetch("http://peremena.rf/api/", { method: 'POST', body: this.prepareOrderData() })
            .then(res => res.json())
            .then(
                (res) => {
                    if( res.result !== 'error' ){
                        this.setState({
                            addOrder_Result: res.message,
                            notice: null
                        });
                        u.clear();
                        this.props.setStage('thankyou');
                    }
                    else{
                        this.setState({notice: null});
                        this.props.setStage('error');
                    }
                },
                // :     ,     catch(),
                //         .
                (error) => {
                    this.setState({
                        error: error + ':   ',
                        notice: 'error'
                    });
                }
            )
    }

    prepareOrderData = () => {
        let [fd, m] = [ new FormData(), this.formData ];
        for(let key in m){
            fd.append(key, m[key])
        }
        fd.append('cart', JSON.stringify(u.getCart()));
        fd.append('action', 'addorder');
        fd.append('user', this.props.authorID);
        fd.append('sessionid', this.props.sessionid);

        return fd;
    }

    validateForm = (formObject) => {
        const data = new FormData(formObject);
        //   /
        let updData = {};
        let formValid = true;
        for(let [name, value] of data) {
            if(value === '')
                formValid = false;
            updData[name] = value;
        }
        this.formData = updData;
        if( formValid && updData.confirm !== undefined )
            this.setState({formReady: true});
        else
            this.setState({formReady: false});
    }

    render(){
        const {formReady,username,phone,email,payment} = this.state

        if(this.state.addOrder_Result){
            if(this.state.addOrder_Result > 0){
                return(
                    <div id="checkout" className="p15">
                        <p className="mt40">  </p>
                        <p>   #{this.state.addOrder_Result}</p>
                    </div>
                )
            }
            else if(this.state.addOrder_Result === 0){}
                return(
                    <div id="checkout" className="p15">
                        <p className="mt40">    . ,   .    .</p>
                    </div>
                )
        }

        return(
            <div id="checkout" className="p15">
                <div className="flex-column item-total__wrapper">
                    <div className="flex-center justify-between">
                        <i className="fas fa-portrait fons80"></i>&nbsp;<span>{u.cartCount}&nbsp;<small>.</small></span>
                    </div>
                    <div className="flex-center justify-between">
                        <i className="fas fa-coins fons80"></i>&nbsp;
                        <span>{u.cartTotal}&nbsp;<i className="fas fa-ruble-sign fons80"></i></span>
                    </div>
                </div>
                <h5 className="">-  </h5>
                <form id="checkout-form" onSubmit={this.handleSubmit}>
                    <div className="form-group">
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <span className="input-group-text"></span>
                            </div>
                            <input onChange={this.handleChange} value={username} name="username" type="text" className="form-control" placeholder="  ." required />    
                        </div>
                        <small>   </small>
                    </div>
                    <div className="form-group">
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <span className="input-group-text"></span>
                            </div>
                            <input onChange={this.handleChange} value={phone} name="phone" type="text" className="form-control" placeholder="+7 (999) 999-99-99" required />    
                        </div>
                        <small>   </small>
                    </div>
                    <div className="form-group">
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <span className="input-group-text">@</span>
                            </div>
                            <input onChange={this.handleChange} vakue={email} name="email" type="text" className="form-control" placeholder="email@mail.com" required />    
                        </div>
                        <small>   </small>
                    </div>

                    <h5 className="mt20">-   </h5>
                    <div className="form-group">
                        <div className="custom-control custom-radio">
                            <input type="radio" id="customRadio1" value="sberbank" name="payment" className="custom-control-input" checked={payment} defaultChecked={true} />
                            <label className="custom-control-label" htmlFor="customRadio1">-</label>
                        </div>
                        <div className="custom-control custom-radio">
                            <input type="radio" id="customRadio3" value="another-bank" name="payment" className="custom-control-input" disabled />
                            <label className="custom-control-label" htmlFor="customRadio3"> </label>
                        </div>
                    </div>

                    <div className="form-group">
                        <div className="custom-control custom-checkbox">
                            <input onChange={this.handleChange} name="confirm" type="checkbox" className="custom-control-input" id="customCheck1" defaultChecked={true} />
                            <label className="custom-control-label" htmlFor="customCheck1">
                                         <a href="#"> </a>.
                            </label>
                        </div>
                    </div>

                    <button
                        className="btn btn-primary btn-lg d-block mt40 mxa"
                        disabled={!formReady}>
                            {this.state.notice === 'loading' ? <i className="fas fa-spinner animation-rotate"></i> : ''}
                    </button>

                </form>
            </div>
        )
    }
}